<template>
    <section v-if="toy" class="toy-edit pages">
        <form class="flex column justify-center align-center">
            <h2 class="edit-title">{{ title }}</h2>
            <img class="toy-img" v-if="toy.imgUrl" :src="toy.imgUrl" alt="" />
            <template v-if="!isLoading">
                <label for="imgUploader">
                    <img class="upload-img"
                        src="https://lh3.googleusercontent.com/proxy/ybV5upOkFlWYVsEN_aWw4UmnHgjOlEG9kzWbDfedBxVYU_jO6UK1bNgdzCGO1FRTT0oCoDPbtweZrB-Qc-dIopuMGygTvvhWdoJznm-RN6g9O_wF-5EuVg4Rr1o"
                        alt=""
                    />
                </label>
                <input
                class="upload-input"
                    type="file"
                    name="img-uploader"
                    id="imgUploader"
                    @change="onUploadImg"
                />
            </template>
            <img
                class="loader"
                v-else
                src="https://i.gifer.com/4V0b.gif"
                alt=""
            />
            <h3>Name</h3>
            <ValidationProvider name="toy.name" rules="required">
                <div class="flex column align-center" slot-scope="{ errors }">
                    <input v-model="toy.name" />
                    <p class="error-msg">{{ errors[0] }}</p>
                </div>
            </ValidationProvider>
            <h3>Type</h3>
            <select class="action" v-model="toy.type">
                <option value="Funny">Funny</option>
                <option value="Adult">Adult</option>
                <option value="Educational">Educational</option>
            </select>

            <h3>Price</h3>
            <ValidationProvider name="toy.price" rules="required">
                <div class="flex column align-center" slot-scope="{ errors }">
                    <input type="number" v-model.number="toy.price" />
                    <p class="error-msg">{{ errors[0] }}</p>
                </div>
            </ValidationProvider>
            <label class="checkbox action"
                >In stock?
                <input type="checkbox" v-model="toy.inStock" />
            </label>
            <div class="edit-btns">
                <el-button type="button" @click="goBack">Back</el-button>
                <el-button @click.prevent="saveToy">Save</el-button>
            </div>
        </form>
    </section>
</template>
<script>
import { toyService } from "../services/toy.service.js";
import { uploadImg } from "../services/img.service.js";
import { extend } from "vee-validate";
import { required } from "vee-validate/dist/rules";
import { ValidationProvider } from "vee-validate";

extend("required", {
    ...required,
    message: "This field is required",
});

export default {
    data() {
        return {
            isEditing: false,
            toy: null,
            isLoading: false,
        };
    },
    computed: {
        title() {
            return this.isEditing ? `Edit toy ${this.toy._id}` : "Add a toy";
        },
    },
    methods: {
        goBack() {
            this.$router.push("/");
        },
        saveToy() {
            if (!this.isEditing) {
                if (!this.toy.name || !this.toy.type || !this.toy.price) {
                    alert("All field are required");
                    return;
                }
            }
            let type = this.isEditing ? "editToy" : "addToy";
            this.$store.dispatch({
                type,
                toy: this.toy,
            });
            this.goBack();
        },
        async onUploadImg(ev) {
            this.isLoading = true;
            const uploadedImg = await uploadImg(ev);
            this.toy.imgUrl = uploadedImg.url;
            this.isLoading = false;
        },
    },
    created() {
        if (!this.$route.params.id) {
            this.toy = toyService.getEmpty();
            this.isEditing = false;
        } else {
            const toyId = this.$route.params.id;
            toyService.getById(toyId).then((toy) => {
                this.isEditing = true;
                this.toy = toy;
            });
        }
    },
    components: {
        ValidationProvider,
    },
};
</script>

<style>
.error-msg {
    color: red;
}
</style>