<template>
    <section class="reviews-page pages container">
        <h2>All reviews</h2>
        <review-list :user="user" :isUserShowen="true" :reviews="reviews"></review-list>
    </section>
</template>

<script>
import reviewList from "@/components/review-list";
import ReviewList from "../components/review-list.vue";

export default {
    computed: {
        reviews() {
            return this.$store.getters.reviews;
        },
        user() {
            return this.$store.getters.loggedinUser;
        }
    },
    created() {
        this.$store.dispatch({
            type: "loadReviews",
        });
    },
    components: {
        reviewList,
    },
};
</script>
