<template>
    <section v-if="user" class="user-details container pages">
        <h2>My reviews</h2>
        <review-list
            :isUserShowen="false"
            :reviews="reviews"
            :user="user"
        ></review-list>
    </section>
</template>

<script>
import { userService } from "../services/user.service";
import reviewList from '@/components/review-list'

export default {
    data() {
        return {
            user: null,
        };
    },
    computed: {
        reviews() {
            return this.$store.getters.reviews;
        },
    },
    async created() {
        const id = this.$route.params.id;
        const user = await userService.getById(id);
        this.user = user;
        console.log("this.user:", this.user);
        this.$store.dispatch({
            type: "loadReviews",
            filterBy: { byUser: this.user._id },
        });
    },
    components: {
        reviewList,
    },
};
</script>