<template>
    <div id="app">
        <header class="app-header flex space-between align-center mb30">
            <div class="logo-container flex">
                <h1 @click="goHome">Mister Toy</h1>
                <span v-if="user">Hello {{ user.username }}</span>
            </div>
            <main-nav :logout="logout"></main-nav>
        </header>
        <router-view class="mb50" />
        <footer class="app-footer">
            <small> 2020 Yuval Beit On - All Rights Reserved</small>
        </footer>
    </div>
</template>


<script>
import mainNav from "@/components/main-nav";
export default {
    methods: {
        goHome() {
            if (this.$route.path === "/") return;
            this.$router.push("/");
        },
        logout() {
            this.$store.dispatch({ type: "logout" });
            this.$router.push("/login");
        },
    },
    computed: {
        user() {
            return this.$store.getters.loggedinUser;
        },
    },
    created() {
        this.$store.dispatch({
            type: "loadToys",
        });
    },
    components: {
        mainNav,
    },
};
</script>

