<template>
    <section v-if="user" class="review-preview flex align-center">
        <el-button
            @click="deleteReview()"
            class="review-btn"
            v-if="user.isAdmin || user"
            >X</el-button
        >
        <li v-if="review" class="flex">
            <star-rating
                read-only
                class="star-rating"
                :increment="0.5"
                :max-rating="5"
                inactive-color="white"
                active-color="gold"
                :star-size="15"
                :border-width="1"
                v-model="review.rating"
                border-color="#999"
            >
            </star-rating>
            <span>Toy: {{ review.aboutToy.name }}</span>
            <span v-if="isUserShowen">By: {{ review.byUser.username }}</span>
            <span>Content: {{ review.content }}</span>
            <small>{{ dateForDisplay }}</small>
        </li>
    </section>
</template>

<script>
import StarRating from "vue-star-rating";

export default {
    props: {
        review: Object,
        isUserShowen: Boolean,
        user: Object,
    },
    methods: {
        deleteReview() {
            this.$store.dispatch({
                type: "removeReview",
                reviewId: this.review._id
            })
        },
    },
    computed: {
        dateForDisplay() {
            return moment(this.review.createdAt).format("MMM Do YY");
        },
    },
    components: {
        StarRating,
    }
};
</script>
