import { toyService } from "../services/toy.service.js";

export const toyStore = {
    strict: true,
    state: {
        toys: [],
        filterBy: null,
        types: ['Adult', 'Funny', 'Educational']

    },
    getters: {
        toys(state) {
            return state.toys;
        },
        types(state) {
            return state.types;
        },
        filterBy(state) {
            return state.filterBy
        }
    },
    mutations: {
        setToys(state, { toys }) {
            state.toys = toys
        },
        deleteToy(state, { toyId }) {
            const idx = state.toys.findIndex(toy => toy._id === toyId)
            state.toys.splice(idx, 1);
        },
        setTypes(state, { types }) {
            state.types = types
        },
        setFilterBy(state, { filterBy }) {
            state.filterBy = filterBy;
        },
        addToy(state, { toy }) {
            state.toys.unshift(toy);
        },
        editToy(state, { toy }) {
            const idx = state.toys.findIndex(currToy => toy._id === currToy._id)
            state.toys.splice(idx, 1, toy);
        },
    },
    actions: {
        setFilterBy(state, { filterBy }) {
            state.commit({ type: 'setFilterBy', filterBy })
            state.dispatch('loadToys')

        },
        async loadToys({ getters, commit }) {
            const toys = await toyService.query(getters.filterBy)
            commit({ type: 'setToys', toys })
            return toys
        },
        async deleteToy({ commit }, { toyId }) {
            const removedToy = await toyService.remove(toyId)
            commit({ type: 'deleteToy', toyId: toyId })
            return removedToy
        },
        async editToy({ commit }, { toy }) {
            console.log('toy from edit store:', toy);
            const savedToy = await toyService.save(toy)
            commit({ type: 'editToy', toy: savedToy })
            return savedToy
        },
        async addToy({ commit }, { toy }) {
            const savedToy = await toyService.save(toy)
            commit({ type: 'addToy', toy: savedToy })
            return savedToy

        }
    },
}