<template>
    <section v-if="user" class="chat-app">
        <h4>Let's talk about {{ toyName }}</h4>
        <span v-if="typing">{{ currUser }} is typing</span>
        <ul ref="msgList" class="msgs clean-list">
            <li
                :class="getClassName(msg.from)"
                v-for="(msg, idx) in msgs"
                :key="idx"
            >
                <span
                    ><strong>{{ msg.from }}: </strong></span
                >
                <span>{{ msg.txt }}</span>
            </li>
        </ul>
        <form
            class="chat-controls flex justify-center"
            @submit.prevent="sendMsg"
        >
            <input @keydown="handleTypeEv" type="text" v-model="msg.txt" />
            <button type="submit">Send</button>
        </form>
    </section>
</template>

<script>
import socketService from "@/services/socket.service";

export default {
    props: {
        topic: String,
        user: String,
        toyName: String,
    },
    data() {
        return {
            msg: { from: this.user, txt: "" },
            msgs: [],
            typing: false,
            currUser: "",
        };
    },
    destroyed() {
        socketService.off("chat addMsg", this.addMsg);
        socketService.terminate();
    },
    methods: {
        addMsg(msg) {
            this.msgs.push(msg);
            this.placeScrollBottom();
        },
        placeScrollBottom() {
            let elMsgList = this.$refs.msgList;
            elMsgList.scrollTop = elMsgList.scrollHeight - elMsgList.clientHeight;
        },
        sendMsg() {
            console.log("Sending", this.msg);
            socketService.emit("chat newMsg", this.msg);
            this.msg = { from: this.user, txt: "" };
        },
        handleTypeEv() {
            socketService.emit("user typing", this.user);
        },
        getClassName(fromUser) {
            return this.user === fromUser ? "msg-preview self" : "msg-preview";
        },
    },
    mounted() {
        this.placeScrollBottom();
    },
    created() {
        socketService.setup();
        socketService.emit("chat topic", this.topic);
        socketService.on("chat addMsg", this.addMsg);

        socketService.on("user typing", (user) => {
            this.currUser = user;
            if (user !== this.user) {
                this.typing = true;
                setTimeout(() => {
                    this.typing = false;
                }, 2000);
            }
        });

        socketService.on("msg history", (msgs) => {
            this.msgs = msgs;
        });
    },
};
</script>


