<template>
    <form class="toy-filter flex end">
        <el-select class="action" @change="setFilter"  v-model="filterBy.type">
            <el-option value="all" label="All"></el-option>
            <el-option v-for="(type, idx) in types" :key="idx" :value="type" :label="type">
            </el-option>
        </el-select>
        <el-select class="action"  @change="setFilter"  v-model="filterBy.sortBy">
            <el-option value="name">Name</el-option>
            <el-option value="price">Price</el-option>
        </el-select>
        <el-checkbox class="action" @change="setFilter" v-model="filterBy.inStock" border>In Stock?</el-checkbox>
    </form>
</template>

<script>
export default {
    data() {
        return {
            filterBy: {
                type: "all",
                inStock: false,
                sortBy: "name",
            },
        };
    },
    computed: {
        types() {
            return this.$store.getters.types;
        },
    },
    methods: {
        setFilter() {
            let filterCopy = JSON.parse(JSON.stringify(this.filterBy));
            if (filterCopy.type === "all") delete filterCopy.type;
            this.$store.dispatch({
                type: "setFilterBy",
                filterBy: filterCopy,
            });
        },
    },
    // created() {
    //     this.setFilter();
    // },
};
</script>

