<template>
    <div class="login pages">
        <!-- <div v-if="loggedinUser">
            <h3>
                Loggedin User:
                {{ loggedinUser.username }}
                <form @submit.prevent="updateUser">
                    <el-input type="text" v-model="userToEdit.username" />
                </form>
                <el-button @click="doLogout">Logout</el-button>
            </h3>
        </div> -->

        <div class="login-subtitle"
            >{{ subtitle }}
            <span class="btn-content" @click.prevent="toggleIsLogin">{{
                subtitleBtn
            }}</span>
        </div>
        <form v-if="isLogin" @submit.prevent="doLogin">
            <el-input
                type="text"
                v-model="loginCred.email"
                placeholder="Email"
            />
            <br />
            <el-input
                type="password"
                v-model="loginCred.password"
                placeholder="Password"
            />
            <br />
            <el-button @click="doLogin">Login</el-button>
        </form>

        <form v-else @submit.prevent="doSignup">
            <el-input
                type="text"
                v-model="signupCred.email"
                placeholder="Email"
            />
            <br />
            <el-input
                type="password"
                v-model="signupCred.password"
                placeholder="Password"
            />
            <br />
            <el-input
                type="text"
                v-model="signupCred.username"
                placeholder="Username"
            />
            <br />
            <el-button @click="doSignup">Signup</el-button>
        </form>
    </div>
</template>

<script>
export default {
    name: "test",
    data() {
        return {
            isLogin: true,
            loginCred: {},
            signupCred: {},
            msg: "",
            userToEdit: {},
        };
    },
    computed: {
        subtitle() {
            return this.isLogin
                ? "Don't have a user yet?"
                : "Already have a user?";
        },
        subtitleBtn() {
            return this.isLogin ? "Signup" : "Login";
        },
        users() {
            return this.$store.getters.users;
        },
        loggedinUser() {
            return this.$store.getters.loggedinUser;
        },
    },
    created() {
        console.log("this.loggedinUser", this.loggedinUser);
    },
    methods: {
        toggleIsLogin() {
            this.isLogin = !this.isLogin;
        },
        async doLogin() {
            const cred = this.loginCred;
            console.log("cred:", cred);
            if (!cred.email || !cred.password)
                return (this.msg = "Please enter user/password");
            await this.$store.dispatch({ type: "login", userCred: cred });
            this.isLogin = false;
            this.loginCred = {};
            this.$router.push("/");
        },
        doLogout() {
            this.$router.push('/');
            this.$store.dispatch({ type: "logout" });
        },
        doSignup() {
            const cred = this.signupCred;
            if (!cred.email || !cred.password || !cred.username)
                return (this.msg = "Please fill up the form");
            this.$store.dispatch({ type: "signup", userCred: cred });
            this.$router.push("/");
        },
    },
    watch: {
        loggedinUser() {
            this.userToEdit = { ...this.loggedinUser };
        },
    },
};
</script>