<template>
    <nav class="main-nav">
        <button @click="toggleNav" class="hamburger">
            <i class="fas fa-bars ham-icon"></i>
        </button>
        <div
            
            :class="{ 'app-links': true, showen: isShowen }"
        >
            <router-link class="link" v-if="!user" to="/login"
                >Login</router-link
            >
            <span class="link" v-else @click="logout">Log Out</span>
            <router-link class="link" to="/">Toys</router-link>
            <router-link class="link" to="/reviews">Reviews</router-link>
            <span
                class="link"
                v-if="user && !user.isAdmin"
                @click="goToUserProfile"
                >Profile</span
            >
            <router-link class="link" v-if="user && user.isAdmin" to="/admin"
                >Admin</router-link
            >
        </div>
    </nav>
</template>

<script>
export default {
    props: {
        logout: Function,
    },
    data() {
        return {
            isNavOpen: false,
        };
    },
    computed: {
        user() {
            return this.$store.getters.loggedinUser;
        },
        isShowen() {
            return this.isNavOpen ? true : false
        },
    },
    methods: {
        goToUserProfile() {
            this.$router.push(`/userdetails/${this.user._id}`);
        },
        toggleNav() {
            this.isNavOpen = !this.isNavOpen;
            console.log(this.isNavOpen);
        },
    },
};
</script>

