import axios from 'axios';

import httpService from './http.service'

export const toyService = {
    query,
    getById,
    remove,
    save,
    getEmpty
}

const BASE_URL = process.env.NODE_ENV === 'production' ? '/api/toy' : '//localhost:3030/api/toy';

async function query(filterBy = {}) {
    let qst = '';
    if (filterBy) {
        qst = getQueryStrPrms(filterBy)
    }
    return httpService.get(`toy/${qst}`)
}

function getQueryStrPrms(filterBy) {
    let str = '?'
    if (filterBy.sortBy) {
        str += `_sort=${filterBy.sortBy}&`
    }
    if (filterBy.inStock) {
        str += `inStock=${filterBy.inStock}&`
    }
    if (filterBy.type) {
        str += `type=${filterBy.type}`
    }
    return str
}

async function getById(id) {
    return httpService.get(`toy/${id}`)
}


async function remove(id) {
    return httpService.delete(`toy/${id}`)
}

function save(toy) {
    const savedToy = (toy._id) ? _update(toy) : _add(toy)
    return savedToy;
}

async function _add(toy) {
    return httpService.post(`toy/`, toy)
}

async function _update(toy) {
    return httpService.put(`toy/${toy._id}`, toy)
}

function getEmpty() {
    return {
        name: '',
        price: 0,
        type: '',
        inStock: false
    }
}