<template>
    <li
        @click.stop="toyDetails(toy._id)"
        class="toy-preview flex column"
        @mouseenter="isHovering = true"
        @mouseleave="isHovering = false"
    >
        <div class="img-container ratio-square">
            <img
                :src="imgSrc"
                alt=""
            />
        </div>
        <h4>{{ toy.name }}</h4>
        <h5>{{ toy.type }}</h5>
        <h3>{{ toy.price }}$</h3>
        <h4 class="inStock" v-if="toy.inStock">In stock</h4>
        <h4 class="outOfStock" v-else>Out of stock</h4>
        <star-rating
            @click.stop="setRating"
            class="star-rating"
            :increment="0.5"
            :max-rating="5"
            inactive-color="white"
            active-color="gold"
            :star-size="15"
            :border-width="1"
            v-model="rating"
            border-color="#999"
        >
        </star-rating>
        <transition name="fade">
            <span v-if="loggedInUser&&loggedInUser.isAdmin"
             v-show="isHovering" class="toy-btns">
                <button @click.stop="editToy">
                    <i class="fas fa-edit fa-icon"></i>
                </button>
                <button @click.stop="emitDelete(toy._id)">
                    <i class="fas fa-trash-alt fa-icon"></i>
                </button>
            </span>
        </transition>
    </li>
</template>


<script>
import StarRating from "vue-star-rating";

export default {
    name: "toy-preview",
    props: {
        toy: Object,
    },
    data() {
        return {
            isHovering: false,
            rating: 0,
        };
    },
    computed: {
        imgSrc() {
           return this.toy.imgUrl? this.toy.imgUrl: "https://www.carrefourbahrain.com/sites/default/files/7.jpg"
        },
        loggedInUser() {
            return this.$store.getters.loggedinUser;
        },
    },
    methods: {
        emitDelete(toyId) {
            this.$emit("deleteToy", toyId);
        },
        toyDetails(toyId) {
            this.$router.push(`/details/${toyId}`);
        },
        editToy() {
            this.$router.push(`/edit/${this.toy._id}`);
        },

        setRating(rating) {
            this.rating = rating;
        },
        getRandomIntInclusive(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min + 1) + min); //The maximum is inclusive and the minimum is inclusive
        },
    },
    components: {
        StarRating,
    },
    created() {
        this.rating = this.getRandomIntInclusive(1, 5);
    },
};
</script>
