<template>
    <section class="toy-app pages">
        <div class="container actions flex space-between">
            <toy-filter> </toy-filter>
            <el-button
                v-if="loggedInUser && loggedInUser.isAdmin"
                class="add-toy-btn"
                @click="addToy"
                >Add Toy</el-button
            >
        </div>
        <toy-list v-if="toys.length" :toys="toys" @deleteToy="deleteToy" />
        <div v-else class="loading flex justify-center">
            <img src="https://i.gifer.com/4V0b.gif" alt="" />
        </div>
    </section>
</template>


<script>
import toyList from "../components/toy-list.vue";
import toyFilter from "../components/toy-filter.vue";

export default {
    name: "toy-app",
    methods: {
        deleteToy(toyId) {
            this.$store.dispatch({
                type: "deleteToy",
                toyId,
            });
        },
        addToy() {
            this.$router.push("/edit");
        },
    },
    computed: {
        toys() {
            return this.$store.getters.toys;
        },
        isPopUpShowen() {
            return this.$store.getters.isPopUpShowen;
        },
        loggedInUser() {
            return this.$store.getters.loggedinUser;
        },
    },
    components: {
        toyList,
        toyFilter,
    },
};
</script>
